'use client';

import { useState } from 'react';
import Navbar from '../components/Navbar';

export default function AddProductPage() {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState<number>(0);
  const [stock, setStock] = useState<number>(0);
  const [message, setMessage] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const product = { name, description, price, stock };

    try {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/products`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(product),
      });

      if (!res.ok) throw new Error('Ürün eklenemedi');

      setMessage('Ürün başarıyla eklendi!');
      setName('');
      setDescription('');
      setPrice(0);
      setStock(0);
    } catch (err: any) {
      console.error(err);
      setMessage('Hata: ' + err.message);
    }
  };

  return (
    <div>
      <Navbar /> {/* Navbar buraya ekleniyor */}

      <main className="p-8 max-w-md mx-auto">
        <h1 className="text-3xl font-bold mb-6">Yeni Ürün Ekle</h1>

        {message && <p className="mb-4 text-green-600">{message}</p>}

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block font-semibold mb-1">Ürün Adı</label>
            <input
              type="text"
              value={name}
              onChange={e => setName(e.target.value)}
              required
              className="w-full border p-2 rounded"
            />
          </div>

          <div>
            <label className="block font-semibold mb-1">Açıklama</label>
            <textarea
              value={description}
              onChange={e => setDescription(e.target.value)}
              required
              className="w-full border p-2 rounded"
            />
          </div>

          <div>
            <label className="block font-semibold mb-1">Fiyat (₺)</label>
            <input
              type="number"
              value={price}
              onChange={e => setPrice(Number(e.target.value))}
              required
              className="w-full border p-2 rounded"
            />
          </div>

          <div>
            <label className="block font-semibold mb-1">Stok</label>
            <input
              type="number"
              value={stock}
              onChange={e => setStock(Number(e.target.value))}
              required
              className="w-full border p-2 rounded"
            />
          </div>

          <button
            type="submit"
            className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          >
            Ekle
          </button>
        </form>
      </main>
    </div>
  );
}

